import React from "react";
import "./contactme.scss";
import { useState } from "react";
import { db } from "../../firebase/firebaseConfig";
import { collection, addDoc } from "firebase/firestore";
import { clear } from "@testing-library/user-event/dist/clear";
import { type } from "@testing-library/user-event/dist/type";

const Contactme = () => {
    const[name, setName] = useState("");
    const[subject, setSubject] = useState("");
    const[content, setContent] = useState("");

    const dbReference = collection(db, "ResponseList")

    const handleSubmit = (e)=>{
        e.preventDefault();
        addDoc(dbReference, {
            content:content,
            name:name,
            subject:subject
        }).then((response)=>{
            if(response){
                alert("Thankyou!! I will go through your comment :)")
                document.querySelector("input").value="";
                document.querySelector("textarea").value="";
            }
        })
    }
  return (
    <form onSubmit={handleSubmit}>

    <div className="contactme-container">
      <div className="contactme-content">
        <input type="text" onChange={(e)=>{setName(e.target.value)}} placeholder="Your name..." required/>
        <input type="text" onChange={(e)=>{setSubject(e.target.value)}} placeholder="Subject" required/>
        <textarea  onChange={(e)=>{setContent(e.target.value)}} rows="10"cols="50" placeholder="Write something here... " required/>
        <button type="submit" value="submit">Send Message</button>
      </div>
      <div className="contactme-content-right">
        <ul>
          <li>
           <h2 id="buildStuff">Get in Touch</h2>
          </li>
          <li>
            <p>Whether you want to get in touch, talk about a project collaboration, or just
              say hi, I'd love to hear from you. Simply fill the from and send me a message.
            </p>
          </li>
        </ul>
      </div>
    </div>
    </form>
  )
}

export default Contactme;